<template>
  <div>
    <div class="img"><img :src="src"/></div>
    <div class="txt"><slot name="txt"/></div>
    <div class="footer"><slot name="foot"/></div>
  </div>
</template>

<script>
  export default {
    name: 'imgLeft',
    props: {
      src: String
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="sass" scoped>
.img
  float: left
  IMG
    height: 300px
.txt
.footer
  clear: both
  text-align: center
  padding-top: 10px
</style>



